  font-family: "Lato", sans-serif;
}

.popover a {
  font-family: inherit;
  font-size: inherit;
}

.popover p,
.popover .p {
  font-size: inherit;
}

.popover-header {
  border: 0;
  padding-bottom: 0;
}

.popover-body {
  max-height: 250px;
  overflow-y: auto;
}

@media (min-width: 576px) {
  .popover {
    --bs-popover-max-width: 450px;
  }
}

.progress-bar {
  box-shadow: 0 0 0.1875rem rgba(34, 34, 34, 0.5);
}

.spinner-border {
  opacity: .5;
}

.table {
  width: auto;
}

.table thead th,
.table thead td {
  background-color: #dbe6ec;
  border: 1px solid white;
}

.table thead td:empty {
  background-color: transparent;
  border-color: transparent;
}

.table thead th {
  font-weight: 400;
}

.table tbody > tr[aria-selected="true"] > td {
  --bs-table-striped-bg: $fhi-core-grey-light-2;
  background-color: #f7f6f5;
}

.table tbody th[rowspan],
.table tbody td[rowspan] {
  vertical-align: middle;
}

.table tbody th {
  background-color: #dbe6ec;
  border: 1px solid white;
  font-weight: 400;
}

.table tbody tr:nth-of-type(even) th {
  --bs-table-bg-type: $fhi-core-blue-grey-3;
}

.table .form-control {
  min-width: 6.25rem;
}

.table .btn {
  font-size: 1.0625rem;
  height: 2.0625rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}

.table .btn [class*="icon-"], .table .btn .fhi-search__btn-icon {
  background-size: 1rem auto;
  margin-right: 0;
}

th,
td {
  font-size: 0.9375rem;
  line-height: 1.25rem;
  transition: background-color 0.25s ease;
}

td .invalid-feedback {
  font-size: 0.9375rem;
}

th[tabindex]:focus-visible {
  box-shadow: 0 0 0 2px #0067c4 inset !important;
}

th[aria-sort] {
  color: #435c68;
  cursor: pointer;
  padding-right: 2rem;
  position: relative;
  user-select: none;
  vertical-align: bottom;
}

th[aria-sort] span {
  line-height: 1.5rem;
  padding-right: 1rem;
}

th[aria-sort] [class*="icon-"], th[aria-sort] .fhi-search__btn-icon {
  background-size: contain;
  margin-left: calc(0.5rem * -1);
  position: absolute;
  transform: translateX(0.25rem);
  vertical-align: bottom;
}

th[aria-sort="ascending"],
th[aria-sort="descending"] {
  box-shadow: 0 0 0 1px currentColor inset !important;
}

.table-striped td {
  border-color: white;
}

.toast-container {
  right: 0;
}

.toast-header {
  padding-top: 0;
  padding-bottom: 0;
}

.toast-header strong {
  font-size: 1.375rem;
}

.toast-header small, .toast-header .small {
  font-size: 12px;
}

.toast-header .btn-close {
  font-size: 0.75rem;
  margin: 0 -1rem 0 0;
  padding: 1rem;
}

.toast-body {
  padding: 0 var(--bs-toast-padding-x) var(--bs-toast-padding-y) var(--bs-toast-padding-x);
}

.tooltip {
  font-family: "Lato", sans-serif;
}

.tooltip-inner {
  text-align: left;
}

/**
  * Color aliases 
  */
.fhi-backdrop-default {
  background-color: #000000;
  opacity: 0.6;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1040;
}

.fhi-backdrop-responsive {
  background-color: #000000;
  opacity: 0.6;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1040;
}

@media (min-width: 1200px) {
  .fhi-backdrop-responsive {
    display: none;
  }
}

.text-error {
  color: #d14641 !important;
}

.fhi-text-ancillary, .fhi-text-placeholder {
  color: #767676;
}

.fhi-text-ancillary-inverse, .fhi-text-placeholder-inverse {
  color: #cfd0cb;
}

.fhi-text-disabled {
  color: #767676;
}

.fhi-text-disabled-inverse {
  color: #cfd0cb;
}

.fhi-text-error {
  color: #dc3545;
}

.fhi-text-error-inverse {
  color: #ffe3e2;
}

*:focus {
  box-shadow: none !important;
  outline: none;
}

*:focus-visible {
  box-shadow: 0 0 0 2px #0067c4 !important;
}

.bg-dark *:focus-visible {
  box-shadow: 0 0 0 2px white !important;
}

